import React from 'react';
const PropTypes = React.PropTypes;

import {getStyle} from 'tool/animationManager';
import LetterTop from 'components/LetterTop';
import LetterPaper from 'components/LetterPaper';
/**
 * 动画
 * @type {Array}
 */
const ANIMATION = [
  {
    start: 800,
    length: 300,
    style: {
      WebkitTransform: 'rotateY({s1}deg) ', 
      transform: 'rotateY({s1}deg) ', 
    },
    
    prams: {
      s1: {
        start: -90,
        count: 90,
      },
    },
  },
];

class LetterPositive extends React.Component {
  static propTypes = {
    step: PropTypes.number.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'LetterPositive';
  }
  render() {
    const {
      step,
    } = this.props;
    const letterClass = ['letter-positive'];
    
    const letterStyle = getStyle(step, ANIMATION, letterClass);

    return (
      <div className={letterClass.join(' ')} style={letterStyle}>
        <div className="letter-positive-bg abs"></div>
        <LetterPaper step={step} />
        <div className="letter-positive-r abs"></div>
        <div className="letter-positive-l abs"></div>
        <div className="letter-positive-b abs"></div>
        <LetterTop step={step} />
      </div>
    );
  }
}

export default LetterPositive;



