import React from 'react';
const PropTypes = React.PropTypes;

import {getStyle} from 'tool/animationManager';
import ColorLetter from 'components/plugins/ColorLetter';
/**
 * 动画
 * @type {Array}
 */
const ANIMATION = [
  {
    start: 500,
    length: 300,
    style: {
      WebkitTransform: 'rotateY({s1}deg) ', 
      transform: 'rotateY({s1}deg) ', 
    },
    prams: {
      s1: {
        start: 0,
        count: 90,
      },
    },
  },
];

class LetterBack extends React.Component {
  static propTypes = {
    step: PropTypes.number.isRequired,
  }
  constructor(props) {
    super(props);
    this.displayName = 'LetterBack';
  }
  render() {
    const {
      step,
    } = this.props;
    const letterClass = ['letter-back'];
    const letterStyle = getStyle(step, ANIMATION, letterClass);

    return (
      <div className={letterClass.join(' ')} style={letterStyle}>
        <div className="letter-back-logo">
          <ColorLetter word="from three" />
        </div>
      </div>
    );
  }
}

export default LetterBack;



